.movie-card {
    box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.2), 0px 1px 1px 0px rgba(0, 0, 0, 0.14),
    0px 2px 1px -1px rgba(0, 0, 0, 0.12);
    border-radius: 4px;
    background-color: #fff;
    flex: 0 0 calc(25% - 16px);
    margin-left: 8px;
    margin-right: 8px;
    margin-top: 16px;
    overflow: hidden;
}

.movie-card .content {
    padding: 8px;
}

.movie-card img {
    display: block;
    max-width: 100%;
}

.movie-card h2 {
    margin-top: 0;
    margin-bottom: 0;
    font-weight: 500;
}

.movie-card p {
    margin-bottom: 0;
    line-height: 1.5;
}